import { useEffect, useState } from "react";
import { Input } from "../../components/Input/Input";
import { useForm } from "react-hook-form";

export function EditPassword() {
    const {
        register,
        handleSubmit,
        getValues,
        formState: { errors }
    } = useForm({
        mode: 'onTouched'
    });

    const [tempData, setTempData] = useState({
        email: '',
        password: ''
    })

    const getPw = {
        'email': 'test@gmail.com',
        'password': '123456'
    }

    useEffect(() => {
        setTempData(getPw);
    }, [])

    const handleEdit = (e) => {
        const { name, value } = e.target;
        setTempData({
            ...tempData,
            [name]: value
        })
    }

    const onSubmit = (data)=>{
        console.log(data)
        //console.log(tempData)
    }

    return (
        <form onSubmit={ handleSubmit(onSubmit)}>
            <Input
                type='email'
                id='email'
                lableName={'Email'}
                errors={errors}
                register={register}
                rules={{
                    required: 'Email 為必填',
                    pattern: {
                        value: /^\S+@\S+$/i,
                        message: 'Email 格式不正確',
                    }
                }}
                value={tempData.email}
                onChange={handleEdit}
            ></Input>
            <Input
                type='password'
                id='password'
                lableName={'密碼'}
                errors={errors}
                register={register}
                rules={{
                    required: '密碼為必填',
                    minLength: {
                        value: 6,
                        message: '密碼不少於 6 碼'
                    },
                }}
                value={tempData.password}
                onChange={handleEdit}
            ></Input>
            <Input
                type='password'
                id='checkPW'
                lableName={'確認密碼'}
                placeholder='最少6位數密碼'
                errors={errors}
                register={register}
                rules={{
                    validate: (match) => {
                        const password = getValues('password');
                        return match === password || '與密碼不符合'
                    }
                }}
            ></Input>
            <div className="d-flex justify-content-center">
                <button type='submit' className="btn btn-primary px-5 py-3">變更</button>
            </div>
        </form>
    )
}


